.quantity {
  display: flex;
  align-content: stretch;
  align-items: stretch;
  justify-content: center;

  height: 30px;

  border: 1px solid $color-matterhorn-3;
}

.quantity__input {
  display: flex;

  width: 28px;

  border: none;
  border-right: 1px solid $color-matterhorn-3;
  border-left: 1px solid $color-matterhorn-3;

  font-size: 16px;
  line-height: 30px;

  height: 100%;
  padding: 0;

  transition: color $trans--300, outline-color $trans--300;
  text-align: center;

  background-color: transparent;

  &::-webkit-inner-spin-button {
    appearance: none;
  }

  &:hover,
  &:focus {
    outline: 1px solid $color-free-speech-red;
    border: none;
  }
}

.quantity__button {
  width: 25px;

  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 0;

  font-size: 20px;

  height: 100%;

  transition: color $trans--300, outline-color $trans--300;

  color: $color-matterhorn-3;
  border: none;
  background: transparent;

  cursor: pointer;

  &:hover,
  &:focus {
    color: $color-free-speech-red;
    outline: none;
  }
}
