.product-card {
  font-size: 14px;
  line-height: 15px;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  width: 220px;
  min-height: 310px;
  padding: 9px 9px 14px;

  letter-spacing: 0.05em;

  border: 1px solid $color-gainsboro;
  background-color: $color-default-white;

  & img {
    width: 100%;
    height: 190px;
    margin: 0 auto;

    object-fit: scale-down;
  }
}

.product-card__rate {
  width: 100%;
  align-items: flex-start;
  font-size: 10px;
  line-height: 15px;

  margin: 2px 0 0 0;

  letter-spacing: 0.1em;

  color: $color-matterhorn-3;
}

.product-card__buttons,
.product-card__info {
  display: flex;
  align-content: center;
  align-items: flex-start;
  justify-content: space-between;
}

.product-card__buttons {
  margin-top: auto;

  a:focus {
    outline: 1px solid $color-free-speech-red;
    outline-offset: 1px;
  }
}

.product-card__info {
  flex-wrap: wrap;

  margin: 9px 5px 8px 4px;
}

.product-card__title {
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;

  width: 60%;
  margin: 0;
  padding-top: 4px;

  letter-spacing: 0.05em;
  text-overflow: ellipsis;
}

.product-card__price {
  width: 40%;
  margin: 0;
  padding-top: 4px;

  text-align: right;
}
