.form-search {
  position: relative;

  background: inherit;
  max-width: 300px;
}

.form-search__form {
  background: inherit;
  display: flex;
  border: 1px solid $color-snow-2;
  border-radius: 2px;

  box-sizing: border-box;
  transition: border-color $trans--300;

  &:hover,
  &:focus {
    border: 1px solid $color-matterhorn;
  }
}

.form-search__input,
.form-search__submit {
  border: none;
  outline: none;
  background: transparent;
}

.form-search__input {
  padding-right: 20px;

  font-size: 14px;
  line-height: 19px;

  width: 260px;

  color: inherit;

  &::placeholder {
    font-size: 12px;
    line-height: 16px;

    color: $color-silver;
  }
}

.form-search__submit {
  position: relative;
  height: 38px;
  width: 38px;

  @include transition-color;

  padding: 0;
  cursor: pointer;

  color: $color-default-white;

  &:hover,
  &:focus {
    color: $color-free-speech-red;
  }
}

.form-search__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 14px;
  height: 15px;
  transform: translate(-50%, -50%);
}

.form-search__select-list {
  font-size: 14px;
  line-height: 19px;

  position: absolute;
  top: 39px;
  left: 0;

  overflow-y: scroll;

  width: 100%;
  min-height: 100%;
  max-height: 120px;
  margin: 0;
  padding: 2px 15px 5px 38px;

  list-style: none;

  color: inherit;
  border: 1px solid $color-snow-2;
  border-radius: 2px;
  background: inherit;

  appearance: none;

  &::-webkit-scrollbar {
    width: 4px;
  }

  &::-webkit-scrollbar-thumb {
    height: 95px;

    border: 2px solid $color-whisper;
    border-radius: 6px;
    background-color: transparent;
  }

  &:hover,
  &:focus {
    border: 1px solid $color-matterhorn;
  }
}

.form-search__select-item {
  font-size: 14px;
  line-height: 19px;

  padding-top: 4px;
  padding-bottom: 4px;

  cursor: pointer;

  color: inherit;

  @include transition-color;

  &:hover,
  &:focus {
    color: $color-matterhorn;
  }
}
