.main-nav {
  font-size: 14px;
  line-height: 20px;

  display: block;

  margin-right: 27px;

  text-align: left;
  letter-spacing: 1px;
}

.main-nav__toggle {
  display: none;
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;

  width: 390px;

  margin: -10px 0 0 -37px;
  padding: 0;

  list-style: none;
}

.main-nav__link {
  display: block;
  margin-top: 10px;
  margin-left: 37px;
}
