.footer {
  min-height: 275px;
  padding-top: 66px;
  padding-bottom: 15px;

  background-color: $color-seal-brown;
}

.footer__container {
  display: grid;

  grid-template-columns: 220px 145px 283px 150px;
  grid-template-rows: 125px auto;
  grid-template-areas: "logo info links contacts"
    "socials info links contacts";
  grid-column-gap: 25px;
}

.footer__logo {
  margin-right: auto;
  margin-bottom: 55px;

  grid-area: logo;
}

.footer__socials {
  grid-area: socials;
}

.footer__nav-section {
  &--info {
    grid-area: info;
  }

  &--links {
    grid-area: links;
  }

  &--contacts {
    grid-area: contacts;
  }
}

.footer__nav-title {
  font-family: $font-family--secondary;
  font-size: 14px;
  font-weight: 400;
  line-height: 44px;

  margin: 0;

  letter-spacing: 0.13em;

  color: $color-default-white;
}

.footer__nav-content {
  font-family: $font-family--main;
  font-size: 10px;
  line-height: 12px;

  margin: 6px 0 0;

  letter-spacing: 0.1em;

  color: $color-default-white;

  &--font-secondary {
    font-family: $font-family--secondary;
    letter-spacing: 0.03em;
  }

  &--address {
    line-height: 13px;
    margin-top: 0;
    margin-bottom: 0;
  }
}

.footer__span {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.footer__icon {
  align-self: center;
  width: 9px;
  height: 9px;
  margin-right: 3px;
}

.footer__nav-list {
  font-size: 10px;
  line-height: 20px;

  margin: 0;
  padding: 0;

  list-style: none;

  letter-spacing: 0.1em;

  color: $color-default-white;
}
