.tabs {
  display: flex;
  flex-wrap: wrap;
}

.tabs__content {
  width: 100%;
}

.tabs__table {
  width: inherit;
  border-spacing: 0;
  padding-top: 21px;
}

.tabs__table-row {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 8px;
}

.tabs__title {
  text-align: left;
  max-width: 160px;
  padding: 0;
  font-weight: 600;
}

.tabs__value {
  max-width: 160px;
  margin-left: auto;
  text-align: end;
}

.tabs__product-description {
  margin: 0;
  padding-top: 20px;
}

.tabs__button {
  min-width: 190px;
  padding-right: 26px;
  padding-left: 26px;
}
