.catalog-filter {
  display: flex;
  flex-direction: column;
  grid-area: filters;
  width: 100%;
}

.catalog-filter__title {
  font-size: 22px;
  line-height: 22px;
  font-weight: 700;
  margin-top: 8px;
  margin-bottom: 22px;
}

.catalog-filter__block {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 31px 0 29px;
  width: 100%;
  border: none;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: -2px;
    width: 215px;
    height: 1px;
    background-color: $color-gainsboro;
  }

  &:last-child {
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: -2px;
      width: 205px;
      height: 1px;
      background-color: $color-gainsboro;
    }
  }
}

.catalog-filter__block-title {
  float: left;
  font-size: 18px;
  line-height: 25px;
  font-weight: 700;
  width: 100%;
  margin-bottom: 20px;
}

.catalog-filter__price-range {
  display: flex;
  justify-content: space-between;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    display: block;
    width: 15px;
    height: 2px;
    background-color: $color-matterhorn-3;
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
    margin-top: -2px;
  }
}

.catalog-filter__price-range input {
  width: 85px;
  height: 30px;

  &::placeholder {
    color: $color-default-black-1;
  }
}


.catalog-filter__block-item {
  margin-bottom: 10px;

  &:last-child {
    margin-bottom: 0;
  }
}
