.form-review {
  display: flex;
  flex-direction: column;
}

.form-review__wrapper {
  display: flex;
  margin-bottom: 5px;
  padding-top: 23px;
}

.form-review__name-wrapper {
  width: 175px;
  margin-right: 30px;
}

.form-review__label {
  display: inline-block;
  margin-bottom: 5px;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 0.02em;
  color: $color-matterhorn-3;

  &--required {
    position: relative;

    &::before {
      content: "*";
      position: absolute;
      top: -5px;
      right: -10px;
      width: 5px;
      height: 5px;
      font-size: 12px;
      line-height: 16px;

      color: $color-free-speech-red;
    }
  }
}

.form-review__input {
  width: 100%;
  font-size: 14px;
  margin-bottom: 10px;
  padding: 5px 8px;
  background-color: inherit;
  border: 1px solid $color-matterhorn-3;
  border-radius: 2px;

  &--name {
    margin-bottom: 0;
  }

  &--textarea {
    height: 70px;
  }

  &::placeholder {
    font-size: 14px;
    line-height: 15px;
    letter-spacing: 0.02em;
    color: $color-default-black;
  }
}

.form-review__warning {
  display: block;
  text-align: end;
  padding-right: 5px;
  font-size: 10px;
  line-height: 15px;
  letter-spacing: 0.02em;

  color: $color-flamingo;
}

.form-review__button {
  margin-top: 7px;
  margin-left: 70px;
  margin-right: 70px;
}
