.socials {
  .socials__list {
    display: flex;
    flex-wrap: wrap;

    margin: -20px 0 0 -20px;
    padding: 0;

    list-style: none;
  }
}

.socials-item {
  margin-top: 20px;
  margin-left: 20px;
}

.socials__link {
  display: block;

  width: 24px;
  height: 24px;

  color: $color-default-white;

  fill: currentColor;

  @include transition-color;
  @include expand-interactive-area(40px, 40px);

  &:hover {
    color: $color-free-speech-red;
  }

  &:focus {
    color: $color-gainsboro;
  }
}

.socials__icon {
  width: 24px;
  height: 24px;
}
