.modal {
  position: fixed;
  z-index: 1000;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  display: flex;
  visibility: hidden;
  overflow-y: auto;
  flex-direction: column;

  transition: opacity $trans--600, transform $trans--600, visibility $trans--600;
  transform: scale(1.2);
  pointer-events: none;

  opacity: 0;
  background-color: rgba($color-default-black, 0.8);

  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &.is-active {
    visibility: visible;

    transform: scale(1);
    pointer-events: auto;

    opacity: 1;
    background: $color-matterhorn-1;
  }

  &--success {
    .modal__content {
      display: flex;
      flex-wrap: wrap;

      padding-top: 117px;
      padding-bottom: 31px;

      background: url("../img/sprite/bg_modal.png") no-repeat top 0 left 0;
      background-color: $color-snow;
      background-size: 255px 95px;

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/sprite/bg_modal@2x.png");
      }
    }
  }

  &--review {
    .modal__content {
      width: 410px;
      padding-top: 26px;
      padding-bottom: 29px;
    }
  }
}

.modal__wrapper {
  position: relative;

  display: flex;
  align-items: center;
  flex-shrink: 0;
  justify-content: center;

  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}

.modal__close-btn {
  position: absolute;
  top: 23px;
  right: 23px;
}

.modal__close-btn-interactive-area {
  @include expand-interactive-area(60px, 65px);
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  cursor: pointer;
}

.modal__content {
  position: relative;

  overflow: hidden;

  width: 430px;
  padding: 40px;

  border-radius: 4px;
  background-color: $color-snow;
}

.modal__info {
  display: flex;
}

.modal__info-wrapper {
  padding-top: 18px;
  padding-left: 37px;
}

.modal__header {
  margin-top: 0;
  margin-bottom: 17px;

  &--review {
    margin-bottom: 23px;
  }
}

.modal__product-name {
  margin-top: 0;
  margin-bottom: 10px;
}

.modal__img {
  display: block;
  object-fit: contain;
  width: 55px;
  height: 137px;
}

.modal__icon {
  width: 26px;
  height: 20px;
  margin-right: 7px;
  margin-left: 22px;
}

.modal__product-params {
  font-size: 14px;
  line-height: 15px;

  margin-top: 0;
  margin-bottom: 5px;

  letter-spacing: 0.05em;

  &--margin-11 {
    margin-top: 11px;
  }
}

.modal__price-wrapper {
  margin-top: 13px;
  margin-bottom: 0;
}

.modal__price {
  font-size: 20px;
  font-weight: bold;
  line-height: 15px;

  letter-spacing: 0.05em;

  &:last-child {
    margin-left: 17px;
  }
}

.modal__message {
  font-size: 18px;
  font-weight: bold;
  line-height: 20px;

  position: relative;

  width: 255px;
  margin-top: 0;
  margin-bottom: 0;

  text-align: center;
  letter-spacing: 0.02em;
}

.modal__button-container {
  display: flex;
  justify-content: start;
  flex-grow: 1;
  padding-top: 20px;

  .modal__button {
    margin: 0;
    min-width: 170px;
    padding-left: 5px;
    padding-right: 5px;

    &--add {
      margin: 0 auto;
      min-width: 225px;
    }

    &--review {
      margin: 0 auto;
      min-width: 140px;
    }

    &--right {
      margin-left: auto;
    }
  }
}
