@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin transition-color($value: $trans--300) {
  transition: color $value;
}

@mixin expand-interactive-area($width, $height: $width) {
  position: relative;

  &::before {
    position: absolute;
    top: 50%;
    left: 50%;

    width: $width;
    height: $height;

    content: "";
    transform: translate(-50%, -50%);
  }
}
