.coupon {
  display: flex;
  flex-direction: column;
}

.coupon__form {
  display: flex;
  width: 100%;
  justify-content: flex-start;
  align-items: flex-start;
}

.coupon__input {
  width: 180px;

  & input {
    font-size: 14px;
    line-height: 15px;
    padding-right: 9px;
    text-transform: uppercase;
    color: $color-default-black;

    width: 100%;

    flex: 0 0 0;
    height: 40px;

    transition: border-color $trans--300;
    text-align: center;
    letter-spacing: 0.08em;

    border: 2px solid $color-gainsboro;
    border-radius: 2px;

    &::placeholder {
      font-size: 12px;
      font-family: $font-family--main;
      line-height: 15px;
      letter-spacing: 0.05em;
      text-transform: none;
      transform: translate3d(0, -2px, 0);
    }
  }

  & input:hover {
    border-color: $color-matterhorn;
  }
}

.coupon__title {
  font-size: 16px;
  line-height: 1;
  font-weight: 700;

  width: 100%;
  margin: 4px 0 9px;
  padding: 0;

  letter-spacing: 0.05em;
}

.coupon__info {
  width: 100%;
  font-size: 14px;
  line-height: 15px;
  letter-spacing: 0.05em;
  color: $color-default-black;
  margin: 0 0 20px;
}

.coupon__button {
  min-width: 180px;
  letter-spacing: 0.05em;
  border-radius: 0 2px 2px 0;
  padding-top: 8px;
  padding-bottom: 8px;
}
