.catalog {
  display: grid;
  align-content: start;
  align-items: start;
  justify-content: start;

  width: 100%;

  grid-template-columns: 215px 1fr;
  grid-template-rows: min-content 1fr min-content;
  gap: 0 25px;
  grid-template-areas: "filters sorter"
    "filters catalog"
    "filters pagination";
  justify-items: start;
}

.catalog__cards {
  margin: 30px 0 0;
}
