.cart-item {
  position: relative;

  display: grid;
  gap: 10px;
  grid-template-columns: 20px 90px 350px 155px 120px 155px;
  grid-template-areas: "button photo info price quantity total-price";
  align-items: start;
  justify-items: start;

  width: 100%;
  padding: 25px 0 9px 0;

  border-top: 0.7px solid $color-gainsboro;
  border-bottom: 0.7px solid $color-gainsboro;

  &:nth-child(n+2) {
    border-top: none;
  }
}

.cart-item__close-button {
  grid-area: button;
  position: relative;
}

.cart-item__close-button-interactive-area {
  @include expand-interactive-area(50px, 50px);
}

.cart-item__image {
  grid-area: photo;

  display: flex;
  align-items: center;
  align-self: center;
  justify-content: center;

  width: 100%;
  height: 130px;
  margin: 0 auto;

  object-fit: contain;
}

.cart-item__info {
  grid-area: info;
  margin: 26px 0 39px 20px;
}

.cart-item__price {
  grid-area: price;

  font-size: 20px;

  margin-top: 46px;
  margin-left: auto;

  letter-spacing: 0.05em;
}

.cart-item__price-total {
  grid-area: total-price;

  font-size: 20px;
  font-weight: 700;

  margin-top: 46px;
  margin-left: auto;

  text-align: end;
  letter-spacing: 0.05em;
}

.cart-item__quantity {
  grid-area: quantity;

  margin-top: 40px;
  margin-left: auto;
}
