.header {
  display: flex;

  min-height: 110px;
  padding-top: 22px;
  padding-bottom: 12px;

  color: $color-default-white;
  background-color: $color-seal-brown;
}

.header__wrapper {
  display: flex;
  align-content: center;
  align-items: center;

  width: 100%;
  padding-right: 66px;

  background: inherit;
}

.header__logo {
  margin-right: 110px;
}

.header__cart-link {
  position: relative;

  display: block;

  width: 14px;
  height: 14px;
  margin-left: auto;

  color: $color-default-white;

  @include transition-color;
  @include expand-interactive-area(40px, 40px);

  &:hover,
  &:focus {
    color: $color-free-speech-red;
  }
}

.header__cart-icon {
  position: absolute;
  top: 50%;
  left: 50%;

  width: 14px;
  height: 14px;

  transform: translate(-50%, -50%);
}

.header__cart-count {
  font-family: $font-family--secondary;
  font-size: 10px;
  line-height: 12px;

  position: absolute;
  top: -5px;
  right: -14px;

  width: 12px;
  height: 12px;

  text-align: center;

  color: $color-default-white;
  border-radius: 50%;
  background-color: $color-free-speech-red;
}
