.pagination {
  width: 100%;

  grid-area: pagination;
}

.pagination__list {
  display: flex;
  justify-content: flex-end;

  margin: 0 19px 0 0;
  padding: 0;

  list-style: none;
}

.pagination__page {
  font-size: 12px;
  line-height: 20px;

  display: flex;
  align-items: center;
  justify-content: center;

  min-width: 25px;
  height: 25px;
  margin-left: 10px;

  letter-spacing: 0.05em;

  &--active {
    pointer-events: none;

    color: $color-default-white;
    border-radius: 2px;
    background-color: $color-seal-brown;
  }

  &--next a,
  &--prev a {
    padding: 2px 15px 2px 15px;
  }
}

.pagination__page-link {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;

  transition: color $trans--300, border-color $trans--300;

  border: 0.7px solid $color-matterhorn-3;
  border-radius: 2px;

  &:hover {
    color: $color-free-speech-red;
  }

  &:active {
    border-color: $color-matterhorn;
  }
}
