.rate {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  width: 100%;

  color: $color-free-speech-red;

  &--reverse {
    flex-direction: row-reverse;
    justify-content: flex-end;
    margin-left: -3px;
  }

  svg {
    margin-right: 3px;
  }

  label {
    position: relative;
  }
}

.rate__label {
  margin-left: 3px;
}

.rate__count {
  margin-right: auto;

  color: $color-matterhorn-3;
}

.rate:not(:checked) > label {
  float: right;
  width: 18px;
  height: 20px;
  overflow: hidden;
  white-space: nowrap;
  cursor: pointer;
  font-size: 30px;
  color: $color-default-white;
}

.rate:not(:checked) > label::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 20px;
  background: url("../img/sprite/icon-star.svg") no-repeat top 0 left 0;
  background-size: 18px 20px;
}

.rate > input:checked ~ label {
  background: url("../img/sprite/icon-full-star.svg") no-repeat top 0 left 0;
  background-size: 18px 20px;
}

.rate:not(:checked) > label:hover,
.rate:not(:checked) > label:hover ~ label {
  background: url("../img/sprite/icon-full-star.svg") no-repeat top 0 left 0;
  background-size: 18px 20px;
}

.rate > input:focus + label {
  outline: 1px solid $color-default-black;
  outline-offset: -1px;
}

.rate__message {
  position: absolute;
  bottom: -17px;
  left: 7px;
  margin: 0;
  width: 100px;
  font-size: 10px;
  font-weight: normal;
  font-style: normal;
  line-height: 15px;

  letter-spacing: 0.02em;

  color: $color-flamingo;
}
