.form-input {
  & input {
    font-size: 14px;
    line-height: 28px;

    padding-left: 9px;

    transition: border-color $trans--300;
    letter-spacing: 0.05em;

    color: $color-default-black;
    border: 0.7px solid $color-matterhorn-3;
    border-radius: 2px;

    &[type="number"]::-webkit-inner-spin-button {
      appearance: none;
    }

    &[type="number"]:invalid {
      border: 2px solid $color-flamingo;
      outline: none;
    }

    &::placeholder {
      color: $color-default-black;
      opacity: 0.6;
    }

    &:hover {
      border-color: $color-matterhorn-1;
    }

    &:focus {
      outline-color: $color-free-speech-red;
    }
  }
}

.form-input__message {
  font-size: 10px;
  line-height: 15px;
  letter-spacing: 0.05em;
  margin: 4px 2px 0 0;
  text-align: right;

  &--success {
    color: $color-camarone;
  }

  &--error {
    color: $color-flamingo;
  }
}
