  letter-spacing: 0.05em;
  align-self: flex-end; }

.button-cross {
  padding: 0;
  width: 14px;
  height: 14px;
  cursor: pointer;
  transition: outline-color 0.3s ease;
  color: #585757;
  border: none;
  background-color: transparent;
  transition: color 0.3s ease; }
  .button-cross:hover {
    color: #c90606; }
  .button-cross:focus {
    color: #c90606;
    outline: none; }

.button-cross__icon::before, .button-cross__icon::after {
  position: absolute;
  top: 6.5px;
  left: -1.5px;
  width: 17px;
  height: 1px;
  content: "";
  background-color: currentColor; }

