  margin: 0;
  padding: 31px 0 29px;
  width: 100%;
  border: none; }
  .catalog-filter__block::before {
    content: "";
    position: absolute;
    top: 0;
    left: -2px;
    width: 215px;
    height: 1px;
    background-color: #dddada; }
  .catalog-filter__block:last-child::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: -2px;
    width: 205px;
    height: 1px;
    background-color: #dddada; }

