  .form-checkbox input:disabled:hover + label::before {
    pointer-events: none;
    border-color: #dddddd;
    background: #f6f3f3; }

.form-input input {
  font-size: 14px;
  line-height: 28px;
  padding-left: 9px;
  transition: border-color 0.3s ease;
  letter-spacing: 0.05em;
  color: #000000;
  border: 0.7px solid #585757;
  border-radius: 2px; }
