$button-cross-size:                  14px;
$button-cross-icon-width:            17px;
$button-cross-icon-height:           1px;

.button-cross {
  padding: 0;
  width: 14px;
  height: 14px;

  cursor: pointer;
  transition: outline-color $trans--300;

  color: $color-matterhorn-3;
  border: none;
  background-color: transparent;

  @include transition-color;

  &:hover {
    color: $color-free-speech-red;
  }

  &:focus {
    color: $color-free-speech-red;
    outline: none;
  }
}

.button-cross__icon {
  &::before,
  &::after {
    position: absolute;
    top: $button-cross-size/2 - $button-cross-icon-height/2;
    left: $button-cross-size/2 - $button-cross-icon-width/2;

    width: $button-cross-icon-width;
    height: $button-cross-icon-height;

    content: "";

    background-color: currentColor;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}
