.title {
  font-family: $font-family--main;
  font-weight: 700;
  line-height: 1.2;

  padding: 0;

  color: inherit;

  &--bigger {
    font-size: 22px;
    line-height: 30px;
  }

  &--big {
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.05em;
  }

  &--medium {
    font-size: 18px;
    letter-spacing: 0.02em;
  }

  &--medium-20 {
    font-size: 18px;
    line-height: 20px;
    letter-spacing: 0.05em;
  }

  &--little {
    font-size: 16px;
    letter-spacing: 0.05em;
  }

  &--lesser {
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
  }

  &--uppercase {
    text-transform: uppercase;
  }

  &--red {
    color: $color-free-speech-red;
  }
}
