.wrapper {
  position: relative;

  display: flex;
  overflow: hidden;
  flex-direction: column;

  min-height: 100vh;

  &--no-scrollbar {
    height: 100vh;

    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  .header,
  .footer {
    flex-shrink: 0;
  }

  main {
    flex-grow: 1;
  }
}

.hidden {
  display: none;
}

.visually-hidden {
  position: absolute;

  clip: rect(0 0 0 0);

  width: 1px;
  height: 1px;
  margin: -1px;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.scroll-lock-ios {
  position: fixed;

  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

// используйте для скрытия нежелательных анимаций-скачков при поворотах экрана
@keyframes disableBreakpointAnimation {
  0% {
    visibility: hidden;

    opacity: 0;
  }

  100% {
    visibility: hidden;

    opacity: 0;
  }
}
